@import url('https://fonts.googleapis.com/css2?family=Dongle:wght@300&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

#root {
  width: 100vw;
  height: 100vh;
  background-image: url("../public/images/bg.jpg");
}

button {outline: none; border: none; background-color: transparent; cursor: pointer}

li { list-style-type: none }

header {
  display: flex;
  justify-content: space-between;
  height: 15%;

  .audio-player {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 300px;
    height: 100%;
  }

  .weather-info {
    width: 300px;
    height: 100%;
  }
}

.buttons-panel {
  display: flex;
  filter: invert(100%);
  button {

    background-size: cover;
    background-color: transparent;
  }

  .other {
    background-image: url("../public/images/other.png");
    width: 30px;
    height: 30px;
    margin-top: 2px;
  }
  .current {
    background-image: url("../public/images/pause.png");
    width: 35px;
    height: 35px;
  }
  .next {
    transform: rotate(180deg);
  }
}

main {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 60%;
  width: 100%;
  margin: 0 auto;
}

.main-content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  font-size: 55px;
  color: white;

  .current-date {
    display: flex;
    flex-direction: column;
  }

  .timer {
    font-size: 75px;
  }

  .welcome-phrase {
    width: 100vw;
    span { width: 50vw }
  }

  .name-field {
    margin-left: 10px;
    border: none;
    outline: none;
    color: white;
    height: 66px;
    width: 300px;
    font-size: 55px;
    background-color: transparent;
  }
}

footer {
  height: 15%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.quotes {
  button {
    display: flex;
    margin: 0 auto;
    width: 40px;
    height: 40px;
    background-image: url("../public/images/reload.png");
    background-size: cover;
    filter: invert(100%);
  }
  p {
    text-align: center;
    color: white;
    font-family: 'Dongle', sans-serif;
    font-size: 35px;
  }
}